@import url("https://fonts.googleapis.com/css2?family=Verdana+Pro:wght@400;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --hci-subheader: "Verdana Pro";

  /* font sizes */
  --hci-header-size: 20px;
  --hci-subheader-size: 14px;

  /* Colors */
  --text-and-neutrals-hci-neutral: #fbf7f4;
  --text-and-neutrals-text-white: #fff;
  --primary-primary-3: #ff9380;
  --text-and-neutrals-text-black: #221212;
  --primary-primary-2: #ffb6a9;
  --color-black: #000;
  --primary-primary-1: #ffd0c8;
  --tertiary-tertiary-5: #683985;
  --color-red: #dd0707;
  --tertiary-tertiary-2: #ba91d3;
  --tertiary-tertiary-3: #9e6ebb;
  --tertiary-tertiary-4: #8550a6;
  --tertiary-tertiary-1: #d5c0e1;
  --status-status-disabled: #c8c8c8;
  --status-status-error: #e94916;
  --status-status-success: #2ac267;
  --secondary-secondary-3: #ffeea3;
  --status-status-information: #8cd2f0;

  /* Gaps */
  --gap-xs: 12px;
  --gap-11xs: 2px;

  /* border radiuses */
  --br-9xs: 4px;
  --br-3xs: 10px;
  --br-7xs: 6px;

  /* Effects */
  --dropshadow-midimpact: 0px 4px 4px rgba(34, 18, 18, 0.2);
  --dropshadow-lowimpact: 0px 2px 4px rgba(34, 18, 18, 0.2);
  --dropshadow-highimpact: 0px 4px 4px rgba(34, 18, 18, 0.4);
}
