.camerascreenChild {
  position: absolute;
  top: 612px;
  left: 259px;
  width: 60px;
  height: 60px;
  opacity: 0.5;
}
.camera,
.header,
.icon {
  position: absolute;
  top: 7px;
  left: 332px;
  width: 35px;
  height: 35px;
}
.camera,
.header {
  top: 0;
  left: 0;
  width: 375px;
  height: 50px;
}
.header {
  border-radius: var(--br-9xs) var(--br-9xs) 0 0;
  background-color: var(--primary-primary-3);
}
.camera {
  display: flex;
  align-items: center;
  justify-content: center;
}
.icon1,
.icon2 {
  position: absolute;
  top: 7px;
  left: 8px;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
.icon2 {
  left: 332px;
}
.camerascreen {
  position: relative;
  border-radius: var(--br-9xs);
  width: 100%;
  height: 667px;
  overflow: hidden;
  background-image: url(/public/loadingscreen@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: var(--hci-header-size);
  color: var(--text-and-neutrals-text-white);
  font-family: var(--hci-subheader);
}
