.loadingscreenChild {
  position: absolute;
  top: 567px;
  left: 127px;
  width: 120px;
  height: 120px;
  opacity: 0.5;
}
.footer {
  top: 617px;
  left: 0;
  border-radius: 0 0 var(--br-9xs) var(--br-9xs);
  background-color: var(--tertiary-tertiary-5);
  width: 375px;
  height: 50px;
  display: none;
  opacity: 0.5;
}
.footer,
.header,
.icon {
  position: absolute;
}
.icon {
  height: 11.99%;
  width: 21.33%;
  top: 87.56%;
  right: 39.2%;
  bottom: 0.45%;
  left: 39.47%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.header {
  top: 0;
  left: 0;
  border-radius: var(--br-9xs) var(--br-9xs) 0 0;
  background-color: var(--primary-primary-3);
  width: 375px;
  height: 50px;
}
.icon1 {
  top: 7px;
  left: 332px;
  width: 35px;
  height: 35px;
}
.camera,
.icon1,
.icon2 {
  position: absolute;
}
.icon2 {
  top: 7px;
  left: 8px;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
.camera {
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 375px;
  height: 50px;
}
.cameraflipIcon,
.cropIcon {
  position: absolute;
  top: 612px;
  left: 259px;
  width: 60px;
  height: 60px;
}
.cropIcon {
  left: 55px;
}
.loadingscreenItem {
  position: absolute;
  top: 50px;
  left: -1px;
  background-color: var(--text-and-neutrals-text-black);
  width: 375px;
  height: 617px;
  opacity: 0.5;
}
.loadingPaletteIcon {
  position: absolute;
  top: 279px;
  left: 148px;
  width: 80px;
  height: 80px;
}
.loadingscreen {
  position: relative;
  border-radius: var(--br-9xs);
  width: 100%;
  height: 667px;
  overflow: hidden;
  background-image: url(/public/loadingscreen@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  cursor: pointer;
  text-align: center;
  font-size: var(--hci-header-size);
  color: var(--text-and-neutrals-text-white);
  font-family: var(--hci-subheader);
}
