.helpfulPopup,
.popupInfoChild {
  position: absolute;
  top: 0;
  left: 0;
  width: 120px;
  height: 30px;
}
.popupInfoChild {
  border-radius: var(--br-9xs);
  background-color: var(--text-and-neutrals-text-white);
  border: 1px solid var(--status-status-information);
  box-sizing: border-box;
}
.helpfulPopup {
  display: flex;
  align-items: center;
  justify-content: center;
}
.popupInfo {
  position: relative;
  box-shadow: var(--dropshadow-lowimpact);
  width: 120px;
  height: 30px;
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
  text-align: center;
  font-size: var(--hci-subheader-size);
  color: var(--text-and-neutrals-text-black);
  font-family: var(--hci-subheader);
}
