.backgroundFull {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--text-and-neutrals-text-white);
  border: 1px solid var(--primary-primary-1);
  box-sizing: border-box;
}
.background {
  position: absolute;
  top: 60px;
  left: 20px;
  width: 335px;
  height: 530px;
}
.footerIcon {
  top: 619px;
  left: 0;
  width: 375px;
  height: 48px;
}
.footerIcon,
.header1,
.icon {
  position: absolute;
}
.header1 {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-9xs) var(--br-9xs) 0 0;
  background-color: var(--primary-primary-3);
}
.icon {
  height: 72.92%;
  width: 9.33%;
  top: 14.58%;
  right: 1.6%;
  bottom: 12.5%;
  left: 89.07%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.exampleText {
  height: 100%;
  width: 59.73%;
  top: 0;
  left: 20.27%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.exampleText,
.header,
.icon1 {
  position: absolute;
}
.icon1 {
  height: 72.92%;
  width: 9.33%;
  top: 14.58%;
  right: 89.07%;
  bottom: 12.5%;
  left: 1.6%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  cursor: pointer;
}
.header {
  top: 0;
  left: 0;
  width: 375px;
  height: 48px;
  font-size: var(--hci-header-size);
  color: var(--text-and-neutrals-text-white);
}
.icon2,
.icon3,
.icon4 {
  position: absolute;
  top: 459px;
  width: 80px;
  height: 80px;
}
.icon2 {
  left: 52px;
  cursor: pointer;
}
.icon3,
.icon4 {
  left: 148px;
}
.icon4 {
  left: 244px;
  cursor: pointer;
}
.footerLabel {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  font-size: var(--hci-subheader-size);
  font-weight: 600;
  font-family: var(--hci-subheader);
  color: var(--text-and-neutrals-text-black);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.labelFooter {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: absolute;
  top: 539px;
  left: 42px;
  width: 100px;
  height: 24px;
}
.footerLabel1 {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}
.labelFooter1,
.labelFooter2 {
  position: absolute;
  top: 539px;
  left: 138px;
  width: 100px;
  height: 24px;
}
.labelFooter2 {
  left: 243px;
  width: 80px;
}
.savedcolorscreen {
  position: relative;
  border-radius: var(--br-9xs);
  background-color: var(--text-and-neutrals-hci-neutral);
  width: 100%;
  height: 667px;
  overflow: hidden;
  text-align: center;
  font-size: var(--hci-subheader-size);
  color: var(--text-and-neutrals-text-black);
  font-family: var(--hci-subheader);
}
