.popupChild,
.popupItem {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-3xs);
  width: 322px;
  height: 235px;
}
.popupItem {
  border-radius: var(--br-3xs) var(--br-3xs) 0 0;
  background-color: var(--status-status-error);
  height: 46px;
}
.areYouSure,
.doYouWant {
  position: absolute;
  left: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 320px;
}
.areYouSure {
  top: 0;
  font-size: var(--hci-header-size);
  height: 46px;
}
.doYouWant {
  top: 46px;
  color: var(--text-and-neutrals-text-black);
  height: 118px;
}
.button,
.nobuttonChild {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
.nobuttonChild {
  right: 0;
  bottom: 0;
  border-radius: var(--br-7xs);
  background-color: var(--status-status-error);
}
.button {
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nobutton,
.yesbuttonChild {
  position: absolute;
  top: 165px;
  left: 24px;
  width: 125px;
  height: 46px;
}
.yesbuttonChild {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-7xs);
  background-color: var(--text-and-neutrals-text-white);
  border: 2px solid var(--status-status-error);
  box-sizing: border-box;
}
.yesbutton {
  position: absolute;
  top: 165px;
  left: 173px;
  width: 125px;
  height: 46px;
  cursor: pointer;
  color: var(--status-status-error);
}
.popup {
  position: relative;
  box-shadow: var(--dropshadow-highimpact);
  width: 322px;
  height: 235px;
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
  text-align: center;
  font-size: var(--hci-subheader-size);
  color: var(--text-and-neutrals-text-white);
  font-family: var(--hci-subheader);
}
