.tileWhiteMidimpact {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--text-and-neutrals-text-white);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
}
.tileChild {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: var(--primary-primary-2);
  height: 17.78%;
  width: 100%;
  right: 0;
  bottom: 82.22%;
  border-radius: var(--br-3xs) var(--br-3xs) 0 0;
}
.label,
.tile,
.tileChild {
  position: absolute;
  top: 0;
  left: 0;
}
.label {
  height: 17.78%;
  width: 100%;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tile {
  width: 135px;
  height: 135px;
}
.icon,
.skintonetile {
  position: absolute;
  top: 38px;
  left: 27px;
  width: 80px;
  height: 80px;
}
.skintonetile {
  top: 435px;
  left: 41px;
  width: 135px;
  height: 135px;
  text-align: center;
  font-size: var(--hci-subheader-size);
  color: var(--text-and-neutrals-text-black);
  font-family: var(--hci-subheader);
}
