.tileWhiteLowimpact {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-3xs);
  background-color: var(--color-red);
  box-shadow: var(--dropshadow-midimpact);
}
.ffffff,
.tile {
  position: absolute;
}
.ffffff {
  height: 16.67%;
  width: 45%;
  top: 100%;
  left: 27.33%;
  font-size: var(--hci-header-size);
  display: flex;
  font-family: var(--hci-subheader);
  color: var(--text-and-neutrals-text-black);
  text-align: center;
  align-items: center;
  justify-content: center;
}
.tile {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  top: 78px;
  left: 38px;
  width: 300px;
  height: 300px;
}
