.house2Point1,
.rectangleIcon {
  top: 0;
  left: 0;
  height: 672px;
  object-fit: cover;
}
.house2Point1 {
  position: absolute;
  width: 897px;
}
.rectangleIcon {
  width: 375px;
}
.footer,
.icon,
.rectangleIcon {
  position: absolute;
}
.footer {
  top: 617px;
  left: 0;
  border-radius: 0 0 var(--br-9xs) var(--br-9xs);
  background-color: var(--tertiary-tertiary-5);
  width: 375px;
  height: 50px;
  display: none;
  opacity: 0.5;
}
.icon {
  top: 7px;
  left: 332px;
  width: 35px;
  height: 35px;
}
.header {
  top: 0;
  left: 0;
  border-radius: var(--br-9xs) var(--br-9xs) 0 0;
  background-color: var(--primary-primary-3);
  width: 375px;
  height: 50px;
}
.camera,
.header,
.icon1 {
  position: absolute;
}
.camera {
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 375px;
  height: 50px;
}
.icon1 {
  top: 7px;
  left: 8px;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
.cameraflipIcon {
  position: absolute;
  top: 614px;
  left: 260px;
  width: 60px;
  height: 60px;
}
.cameraIcon,
.filterIcon {
  position: absolute;
  top: 569px;
  left: 128px;
  width: 120px;
  height: 120px;
}
.filterIcon {
  top: 614px;
  left: 56px;
  width: 60px;
  height: 60px;
}
.loadingcolorblindChild {
  position: absolute;
  top: 49px;
  left: 0;
  background-color: var(--text-and-neutrals-text-black);
  width: 375px;
  height: 617px;
  opacity: 0.5;
}
.loadingPaletteIcon {
  position: absolute;
  top: 278px;
  left: 149px;
  width: 80px;
  height: 80px;
}
.loadingcolorblind {
  position: relative;
  border-radius: var(--br-9xs);
  background-color: var(--text-and-neutrals-hci-neutral);
  width: 100%;
  height: 667px;
  overflow: hidden;
  cursor: pointer;
  text-align: center;
  font-size: var(--hci-header-size);
  color: var(--text-and-neutrals-text-white);
  font-family: var(--hci-subheader);
}
